/* IMPOTS */

@import "css/layouts.css"; 
@import "css/clientPages.css"; 
@import "css/inputs.css"; 
@import "css/storeComponents.css";

/* FONTS */

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Exo:wght@200&display=swap');

/* NOTYF */

@import url('https://cdn.jsdelivr.net/npm/notyf@3/notyf.min.css');


html , body > * {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Exo', sans-serif;
}

/* H1,H2.. Ect' */

.mainTitle {
  font-style: italic;
}

.subTitle  {
  font-style: normal;
}

.clientNavTitle {
  font-style: italic;
}

/* Links, A, NavLink, Nav */

.mainNavBar {
  margin: 20px 0px 0px 0px; 
  padding: 0% 0% 10px 0%;
  font-size: large;
}

.mainNavLink {
  display:block;
  padding: 0;
  margin: 0% 3% 0 3%;
  color: rgb(82, 82, 82);
  font-size: 1.6rem;
  text-decoration: none;
}

.mainNavLink:hover {
  color: #81C3D7;
}


.sideNavigation {
  border-right: 1px solid rgb(240, 240, 240);
  background-color: rgb(250, 250, 250);
}

.clientNavLink {
  color: black;
  font-size: medium;
}

.clientNavLink:hover {
  color: #81C3D7;
}

.genralLink {
  color: black;
  font-weight: bold;
  text-decoration: none;
}

.genralLink:hover {
  color: #81C3D7;
}

/* center */ 

.centerBlock {
  display:flex;
  justify-content:center;
  align-items:center;
}

.centerInline {
  display:flex;
  justify-content:center;
  align-items:center;
}

.rightBlock {
  display:flex;
  justify-content:center;
  align-items:flex-end;
}

.leftBlock {
  margin-left: 20px;
}

.centerText {
  text-align: center;
}


/* Tables */

.tableHead {
  background-color: #81C3D7;
  
}

/* Span */

.errorMSG {
  color: red;
  font-size: small;
  font-weight: bold;
}

.cartNumber {
  color: red;
  font-weight: bolder;
}

.linkSprator {
  font-size: xx-large;
}


/* IMAGES */


.siteLogo {
  height: 80px;
}


.TableCouponImg {
  margin: 0px;
  padding: 0px;
  height:60px;
  height:60px;
  transition: transform .2s; 
}

.TableCouponImg:hover {
  transform: scale(2);
}
