@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo:wght@200&display=swap);
@import url(https://cdn.jsdelivr.net/npm/notyf@3/notyf.min.css);
/* MAIN LAYOUT */

.layout {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 0.4fr 3.4fr 0.2fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "mainHeader"
      "MainViewRouting"
      "mainFooter";
  }
  
  .mainHeader { grid-area: mainHeader; }
  
  .MainViewRouting { grid-area: MainViewRouting; }
  
  .mainFooter { grid-area: mainFooter;}


/* HEADER */

.mainHeader {
    display: grid;
    grid-template-columns: 0.8fr 4fr 1fr 1.1fr;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "headerLogo headerNavigation headerCart headerClient";
      position: -webkit-sticky;
      position: sticky;
      
    top: 0px;
    background-color: white;
    border-bottom: 1px solid  rgb(229, 229, 229);
  }
  
  .headerLogo { grid-area: headerLogo; }
  
  .headerNavigation { grid-area: headerNavigation; }
  
  .headerCart { grid-area: headerCart; }
  
  .headerClient { grid-area: headerClient; }
  

/* FOOTER */  

.mainFooter {
    border-top: 1px solid rgb(229, 229, 229);
    background: white; 
    box-shadow: 0px -7px 19px 0px #B5B5B5;
  }




  

  

/* ADMIN */

.adminView {
    height: 100%;
    display: grid;
    grid-template-columns: 0.3fr 1.7fr;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "adminNavigation adminRouting";
}
  
  
.adminNavigation {
     grid-area: adminNavigation; 
}
  
.adminRouting { 
      grid-area: adminRouting;
}

  
/* COMPANY */

.companyView {
    height: 100%;
    display: grid;
    grid-template-columns: 0.3fr 1.7fr;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "companyNavigation companyRouting";
}
  
  
.companyNavigation { 
      grid-area: companyNavigation;
}
  
.companyRouting { 
    grid-area: companyRouting;
}
  


/* CUSTOMER */
  
.customerView {
    height: 100%;
    display: grid;
    grid-template-columns: 0.3fr 1.7fr;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "customerNavigation customerRouting";
}
  
  
.customerNavigation {
   grid-area: customerNavigation;

}
  
  .customerRouting { 
    grid-area: customerRouting;
}

/* BUTTONS */

button {
    background-color: #3b80b4 ;
    color: white;
    border-radius: 6px;
    border-style: none;
    outline-style: none;
  }
  
  button:hover {
    background-color: #2b5e84;
    color: #fff;
  }
  
  
  .formButton {
    border-radius: 6px ; 
    width: 100px;
    height: 30px;
  }
  
  .formButtonReset {
    border-radius: 6px ; 
    width: 100px;
    height: 30px;
    background-color:red;
    color: rgb(255, 255, 255);
    margin: 0px 5px 0px 10px;
  }
  
  .deleteButton {
    background: rgb(255, 0, 0);
    color: rgb(255, 255, 255);
  }
  
  .loggerButton {
    width: 60px;
    height: 30px;
  }
  
  .signUpButton { 
    margin: 0px 0px 0px 10px;
    width: 65px;
    height: 35px;
    background: white;
    color: black;
    border: 1px solid black;
  }
  

/* storeComponents */

/* Store Page  */

.cardsHolderGrid {
  display: grid; 
  grid-template-columns: 0.5fr 5fr 0.5fr; 
  grid-template-rows: 1fr 0.1fr; 
  grid-gap: 0px 0px; 
  gap: 0px 0px; 
  grid-template-areas: 
    ". cardsHolder ."; 
}

.cardsHolder {
    grid-area: cardsHolder;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-auto-rows: auto;
    grid-gap: 1rem;
  }


/* CouponCard*/

.couponCard {
    margin-right: 5px;
    margin-left: 5px;
    background-color: #fafafa;
    border-radius: 5px;
    text-align: center;
    
    display: grid; 
    grid-template-columns: 1fr; 
    grid-template-rows: 180px 170px 25px 35px;   
    grid-template-areas: 
      "cardImg"
      "cardInfo"
      "cardPrice"
      "cardButton"; 
  }
  
  .cardButtonButton {
    width: 100%;
    height: 100%;
   border-radius: 0px ;
   border-bottom-left-radius: 8px;
   border-bottom-right-radius: 8px;
  }
  
  .cardImg { grid-area: cardImg;  
    border-radius: 0px ;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
  }

  .cardInfo { grid-area: cardInfo; }
  .cardPrice { grid-area: cardPrice; }
  .cardButton { 
    grid-area: cardButton; 
    border-radius: 0px ;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
  }
  
  .couponImg {
    max-width: 100%;
    max-height: 100%;
    height: 180px;
    width: 100% !important;
    border-radius: 0px ;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
  }

  /* Cart */
  
  .cartDrawer {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2.5fr 0.2fr 50px;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "cartDrawerItems"
      "cartDrawerTotal"
      "cartDrawerPurchase";
    
  }

 
  
  .cartDrawerItems { grid-area: cartDrawerItems; }
  
  .cartDrawerTotal { 
    grid-area: cartDrawerTotal;
    display: grid; 
    grid-template-columns: 1fr 1fr 1fr; 
    grid-template-rows: 1fr; 
    grid-gap: 0px 0px; 
    gap: 0px 0px; 
    grid-template-areas: 
      "totalPrice . totalItems"; 
  }
  
  .totalItems { grid-area: totalItems; }
  .totalPrice { grid-area: totalPrice; }
  .cartDrawerPurchase { grid-area: cartDrawerPurchase; }
  

  .cartDrawerPurchaseButton {
    width: 100%;
    height: 100%;
    border-radius: 0px ;

  }

  /* CouponLine */

  .cardLine {
    display: grid;
    grid-template-columns: 0.6fr 2.2fr 0.2fr;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "lineImg lineTitle lineRemove";
      margin: 3px 3px 5px 3px;
      border: 1px solid gray;
  }
  
  .lineImg {
    height: 65px;
    width: 65px;
     grid-area: lineImg;
    border-right:1px solid black ; }
  
  .lineTitle { 
    display: grid;
    grid-template-columns: 1.7fr 1fr;
    grid-template-rows: 1.2fr 0.8fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "linetitletitle ."
      "linetitledesc linetitleprice";
    grid-area: lineTitle;
   }
  
   .linetitletitle { grid-area: linetitletitle; }

  .linetitledesc { grid-area: linetitledesc; }

  .linetitleprice { grid-area: linetitleprice; }

  .lineRemove { grid-area: lineRemove; }
  
  .cartImg {
    height: 100%;
    width: 100%;

    }


    .lineRemoveButton {
      width: 100%;
      height: 100%;
      border-radius: 0px ;
  
    }

    .dropdownContent {
      display: none;

      /* Position it right below the trigger element */
      left: 0;
      padding-top: 4px;
      position: absolute;
      top: 100%;

      /* It should be on the top of other elements */
      background-color: #fff;
      z-index: 9999;

      /* Size */
      height: 100px;
      width: 200px;
    }

    .seeDesc {
      color: lightblue;
      font-size: x-small;
      font-weight: bolder;
    }


    .linetitledesc {
      position: relative;
  } 

    .linetitledesc:hover .dropdownContent {
      display:flex;
      justify-content:center;
      align-items:center;
      border: 2px solid whitesmoke;
      text-align: center;
  }
